import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { VerifyDetailComponent } from './verify-detail.component';
import { VerifyDetailService } from './verify-detail.service';
import { LocaleModule } from '@farris/ui-locale';

@NgModule({
  declarations: [VerifyDetailComponent],
  imports: [
    CommonModule,
    FormsModule,
    LocaleModule
  ],
  providers: [
    VerifyDetailService
  ],
  exports: [VerifyDetailComponent],
  entryComponents: [ VerifyDetailComponent]
})
export class VerifyDetailModule { }
